@font-face {
    font-family: 'Museo Sans Light';
    src: url('../fonts/MuseoSansCyrl-300Italic.eot');
    src: url('../fonts/MuseoSansCyrl-300.eot?#iefix') format('embedded-opentype'),
    url('../fonts/MuseoSansCyrl-300.woff') format('woff'),
    url('../fonts/MuseoSansCyrl-300.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'Museo Sans Bold Italic';
    src: url('../fonts/MuseoSansCyrl-700Italic.eot');
    src: url('../fonts/MuseoSansCyrl-700Italic.eot?#iefix') format('embedded-opentype'),
    url('../fonts/MuseoSansCyrl-700Italic.woff') format('woff'),
    url('../fonts/MuseoSansCyrl-700Italic.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

@mixin reg {
    font-family: 'Museo Sans Light', sans-serif;
    font-weight: 300;
}

@mixin bold {
    font-family: 'Museo Sans Bold Italic', sans-serif;
    font-weight: 700;
}